'use client';

import { useState, useEffect } from 'react';
import { apiGet, apiPost, apiPut, apiDelete } from '../../lib/api';
import { getRole, getAgencyCode } from '../../lib/session';
import { Header } from '@/components/Header';

interface Operator {
  id: string;
  username: string;
  name: string;
  operatorCode: string;
  status: string;
  createdAt: string;
  operatorLinks: Array<{
    group: {
      id: string;
      name: string;
    };
  }>;
}

interface Group {
  id: string;
  name: string;
}

export default function OperatorsPage() {
  const [operators, setOperators] = useState<Operator[]>([]);
  const [groups, setGroups] = useState<Group[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [active, setActive] = useState<boolean>(false);
  const [editingOperator, setEditingOperator] = useState<Operator | null>(null);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [formData, setFormData] = useState({
    username: '',
    name: '',
    password: '',
    operatorCode: '',
    groupId: ''
  });

  useEffect(() => {
    const role = getRole();
    if (role !== 'OWNER') {
      setError('–î–æ—Å—Ç—É–ø –∑–∞–±–æ—Ä–æ–Ω–µ–Ω–æ');
      setLoading(false);
      return;
    }

    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      const [operatorsData, groupsData, shiftStatus] = await Promise.all([
        apiGet<Operator[]>('/users/operators'),
        apiGet<Group[]>('/groups'),
        apiGet<{ active: boolean }>('/api/shifts/is-active')
      ]);
      setOperators(operatorsData);
      setGroups(groupsData);
      setActive(!!shiftStatus?.active);
    } catch (err) {
      setError('–ü–æ–º–∏–ª–∫–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –¥–∞–Ω–∏—Ö');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = async (e: React.FormEvent) => {
    e.preventDefault();
    const agencyCode = getAgencyCode();
    if (!agencyCode) {
      setError('–ù–µ –≤–¥–∞–ª–æ—Å—è –æ—Ç—Ä–∏–º–∞—Ç–∏ –∫–æ–¥ –∞–≥–µ–Ω—Ü—ñ—ó');
      return;
    }

    try {
      await apiPost('/users/operator', {
        agencyCode,
        ...formData
      });
      setShowCreateForm(false);
      setFormData({ username: '', name: '', password: '', operatorCode: '', groupId: '' });
      loadData();
    } catch (err) {
      setError('–ü–æ–º–∏–ª–∫–∞ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞');
      console.error(err);
    }
  };

  const handleUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingOperator) return;

    try {
      await apiPut(`/users/operators/${editingOperator.id}`, formData);
      setEditingOperator(null);
      setFormData({ username: '', name: '', password: '', operatorCode: '', groupId: '' });
      loadData();
    } catch (err) {
      setError('–ü–æ–º–∏–ª–∫–∞ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞');
      console.error(err);
    }
  };

  const handleDelete = async (operatorId: string) => {
    if (!confirm('–í–∏ –≤–ø–µ–≤–Ω–µ–Ω—ñ, —â–æ —Ö–æ—á–µ—Ç–µ –≤–∏–¥–∞–ª–∏—Ç–∏ —Ü—å–æ–≥–æ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞?')) return;

    try {
      await apiDelete(`/users/operators/${operatorId}`);
      loadData();
    } catch (err) {
      setError('–ü–æ–º–∏–ª–∫–∞ –≤–∏–¥–∞–ª–µ–Ω–Ω—è –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞');
      console.error(err);
    }
  };

  const startEdit = (operator: Operator) => {
    setEditingOperator(operator);
    setFormData({
      username: operator.username,
      name: operator.name,
      password: '',
      operatorCode: operator.operatorCode,
      groupId: operator.operatorLinks[0]?.group.id || ''
    });
  };

  const cancelEdit = () => {
    setEditingOperator(null);
    setShowCreateForm(false);
    setFormData({ username: '', name: '', password: '', operatorCode: '', groupId: '' });
  };

  if (loading) return <div className="p-6">–ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è...</div>;
  if (error) return <div className="p-6 text-red-600">{error}</div>;

  return (
    <div className="flex flex-col h-screen">
      <Header showBackButton={true} showChatsButton={true} isActive={active} currentPath="/operators" />

      {/* –û—Å–Ω–æ–≤–Ω–∏–π –∫–æ–Ω—Ç–µ–Ω—Ç */}
      <div className="flex-1 overflow-y-auto p-6 custom-scroll">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold">–£–ø—Ä–∞–≤–ª—ñ–Ω–Ω—è –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞–º–∏</h1>
          <button
            onClick={() => setShowCreateForm(true)}
            className="bg-primary text-white px-4 py-2 rounded hover:opacity-90"
          >
            –î–æ–¥–∞—Ç–∏ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞
          </button>
        </div>

      {/* –§–æ—Ä–º–∞ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è/—Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—è */}
      {(showCreateForm || editingOperator) && (
        <div className="mb-6 p-4 border rounded-lg bg-gray-50">
          <h3 className="text-lg font-semibold mb-4">
            {editingOperator ? '–†–µ–¥–∞–≥—É–≤–∞—Ç–∏ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞' : '–°—Ç–≤–æ—Ä–∏—Ç–∏ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞'}
          </h3>
          <form onSubmit={editingOperator ? handleUpdate : handleCreate} className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium mb-1">–Æ–∑–µ—Ä–Ω–µ–π–º</label>
              <input
                type="text"
                value={formData.username}
                onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                className="w-full p-2 border rounded"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">–Ü–º&apos;—è</label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full p-2 border rounded"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">
                –ü–∞—Ä–æ–ª—å {editingOperator && '(–∑–∞–ª–∏—à–∏—Ç–∏ –ø–æ—Ä–æ–∂–Ω—ñ–º, —â–æ–± –Ω–µ –∑–º—ñ–Ω—é–≤–∞—Ç–∏)'}
              </label>
              <input
                type="password"
                value={formData.password}
                onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                className="w-full p-2 border rounded"
                required={!editingOperator}
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">–†–µ—Ñ –∫–æ–¥</label>
              <input
                type="text"
                value={formData.operatorCode}
                onChange={(e) => setFormData({ ...formData, operatorCode: e.target.value })}
                className="w-full p-2 border rounded"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">–ì—Ä—É–ø–∞</label>
              <select
                value={formData.groupId}
                onChange={(e) => setFormData({ ...formData, groupId: e.target.value })}
                className="w-full p-2 border rounded"
              >
                <option value="">–ë–µ–∑ –≥—Ä—É–ø–∏</option>
                {groups.map((group) => (
                  <option key={group.id} value={group.id}>
                    {group.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="col-span-2 flex gap-2">
              <button
                type="submit"
                className="bg-primary text-white px-4 py-2 rounded hover:opacity-90"
              >
                {editingOperator ? '–û–Ω–æ–≤–∏—Ç–∏' : '–°—Ç–≤–æ—Ä–∏—Ç–∏'}
              </button>
              <button
                type="button"
                onClick={cancelEdit}
                className="bg-gray-500 text-white px-4 py-2 rounded hover:opacity-90"
              >
                –°–∫–∞—Å—É–≤–∞—Ç–∏
              </button>
            </div>
          </form>
        </div>
      )}

      {/* –¢–∞–±–ª–∏—Ü—è –æ–ø–µ—Ä–∞—Ç–æ—Ä—ñ–≤ */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse border border-gray-300">
          <thead>
            <tr className="bg-gray-100">
              <th className="border border-gray-300 px-4 py-2 text-left">–Æ–∑–µ—Ä–Ω–µ–π–º</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–Ü–º&apos;—è</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–†–µ—Ñ –∫–æ–¥</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–ì—Ä—É–ø–∞</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–°—Ç–∞—Ç—É—Å</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–°—Ç–≤–æ—Ä–µ–Ω–æ</th>
              <th className="border border-gray-300 px-4 py-2 text-left">–î—ñ—ó</th>
            </tr>
          </thead>
          <tbody>
            {operators.map((operator) => (
              <tr key={operator.id} className="hover:bg-gray-50">
                <td className="border border-gray-300 px-4 py-2">{operator.username}</td>
                <td className="border border-gray-300 px-4 py-2">{operator.name}</td>
                <td className="border border-gray-300 px-4 py-2">{operator.operatorCode}</td>
                <td className="border border-gray-300 px-4 py-2">
                  {operator.operatorLinks[0]?.group.name || '–ë–µ–∑ –≥—Ä—É–ø–∏'}
                </td>
                <td className="border border-gray-300 px-4 py-2">
                  <span className={`px-2 py-1 rounded text-sm ${
                    operator.status === 'ACTIVE' ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                  }`}>
                    {operator.status === 'ACTIVE' ? '–ê–∫—Ç–∏–≤–Ω–∏–π' : '–ó–∞–±–ª–æ–∫–æ–≤–∞–Ω–∏–π'}
                  </span>
                </td>
                <td className="border border-gray-300 px-4 py-2">
                  {new Date(operator.createdAt).toLocaleDateString('uk-UA')}
                </td>
                <td className="border border-gray-300 px-4 py-2">
                  <div className="flex gap-2">
                    <button
                      onClick={() => startEdit(operator)}
                      className="bg-blue-500 text-white px-2 py-1 rounded text-sm hover:opacity-90"
                    >
                      –†–µ–¥–∞–≥—É–≤–∞—Ç–∏
                    </button>
                    <button
                      onClick={() => handleDelete(operator.id)}
                      className="bg-red-500 text-white px-2 py-1 rounded text-sm hover:opacity-90"
                    >
                      –í–∏–¥–∞–ª–∏—Ç–∏
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

        {operators.length === 0 && (
          <div className="text-center py-8 text-gray-500">
            –û–ø–µ—Ä–∞—Ç–æ—Ä—ñ–≤ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ
          </div>
        )}
      </div>
    </div>
  );
}
